<template>  
    <div class="messaging" style="padding-top:20px">
      <div class="inbox_msg">
        <div class="inbox_people">
          <div class="headind_srch">
            <div class="recent_heading">
              <h4 >{{support.username}} 
                <i @click="reloadList()" style="cursor:pointer" class="fa fa-retweet ibutton"></i> | 
                <i @click="logout()" style="cursor:pointer" class="fa fa-sign-out ibutton"></i>
              </h4>
            </div>
            <div class="srch_bar">
              <div class="stylish-input-group">
                <input type="text" class="search-bar" v-on:keyup="SearchList()" v-model="txtSearch" placeholder="Search" >
                <span class="input-group-addon">
                <button type="button"> <i class="fa fa-search" aria-hidden="true"></i> </button>
                </span> </div>
            </div>
          </div>
          <div class="inbox_chat">
            <div v-if="showListLoading" class="loading9 inner" style="margin-top:50px"><i></i><i></i><i></i><i></i></div>
            <div v-if="!showListLoading" :class="`ibutton chat_list ${c.asa_id===history.asa_id? 'active_chat' : ''} `" 
                 style="cursor: pointer" v-for="c in custommers" v-bind:key="c.asa_id" 
                 @click="SetHistory(c.asa_id, c.name, c.lineuser_id, c.pictureUrl)">
              <div class="chat_people" id="status-bar">
                <div class="chat_img"> 
                    <img v-if="!c.pictureUrl" src="https://ptetutorials.com/images/user-profile.png" alt="sunil"> 
                    <img v-if="c.pictureUrl" :src="c.pictureUrl" style="border-radius: 50%;" alt="sunil"> 
                </div>
                <div class="chat_ib">
                    <h5>
                      {{c.name }} 
                      <span v-if="c.is_read !== 'Y'" class="badge badge-pill badge-success new_message">{{c.cntUnread}}</span> 
                      <span class="chat_date">{{getTimeAgo(c.create_date)}}</span>
                    </h5>
                  <p>{{c.sent_type === 'image' ? 'ส่งรูปภาพ' : c.lastMsg}}</p>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="mesgs">
          <div class="headind_srch" style="margin-bottom :10px">
            <div class="recent_heading" style="margin-top:-10px">
              <h4>{{history.name}}</h4>
            </div>
          </div>
          <div class="msg_history" id="msg_history">
              <div v-if="showHistoryLoading" class="loading9 inner" style="margin-top:200px;"><i></i><i></i><i></i><i></i></div>
              <div v-if="showHistory && !showHistoryLoading" v-for="m in history.chatDetail" v-bind:key="m.id">
                <div class="incoming_msg"  v-if="m.sent_type">
                    <img class="incoming_msg_img" v-if="!history.pictureUrl" src="https://ptetutorials.com/images/user-profile.png" alt="sunil"> 
                    <img class="incoming_msg_img" v-if="history.pictureUrl" :src="history.pictureUrl" style="border-radius: 50%;" alt="sunil"> 
                    <div class="received_msg">
                        <div class="received_withd_msg">
                            <p v-if="m.sent_type==='text'">{{m.message}}</p>
                            <p v-if="m.previewImageUrl == 'loading'" class="loading"></p>
                            <p v-if="m.previewImageUrl != 'loading' && m.sent_type==='image'" style="background :white">
                                <a href="#open-modal" @click="previewUrl=m.previewImageUrl">
                                    <img style="border-radius: 8px;" :src="m.previewImageUrl" width="250">
                                </a>
                            </p>
                            <span class="time_date"> {{getTimeAgo(m.create_date)}} {{m.is_read!=='Y'? ': unread' : ''}}</span>
                        </div>
                    </div>
                </div>
                <div class="outgoing_msg" v-if="m.response_type">
                    <div class="sent_msg">
                        <p v-if="m.response_type==='text'" :style="m.response_complete === 'N' ? 'background-color: #cde2e8' : ''"> {{m.message}}</p>
                        <p v-if="m.previewImageUrl == 'loading'" class="loading"></p>
                        <p v-if="m.previewImageUrl != 'loading' && m.response_type==='image'" style="background :white">
                            <a href="#open-modal" @click="previewUrl=m.originalContentUrl">
                                <img style="border-radius: 8px;" :src="m.previewImageUrl" width="250">
                            </a>
                        </p>
                        <span class="time_date"> {{m.response_complete === 'N'? 'ส่งไม่สำเร็จ' : getTimeAgo(m.create_date)}}
                            <i class="fa fa-retweet" v-if="m.response_complete === 'N'" @click="resendMessage(m)">|</i> 
                            <i class="fa fa-remove" v-if="m.response_complete === 'N'" @click="deleteMessage(m)"></i>
                        </span> 
                    </div>
                </div>
              </div>
              <div style="padding:50px"></div>
          </div>
          <div class="type_msg">
            <div class="input-group" style="margin:5px">
                <input type="text" style="border: 0" class="form-control" @keyup.enter="onSend(history.asa_id, history.lineuser_id)" v-model="txtMessage" placeholder="Type a message">
                <div class="input-group-append">
                    <button class="btn btn-outline-secondary" @click="onSend(history.asa_id, history.lineuser_id)" type="button"><i class="fa fa-paper-plane-o" aria-hidden="true"></i></button>
                    <button class="btn btn-outline-secondary" onclick="document.getElementById('src').click();" type="button"><i class="fa fa-image" aria-hidden="true"></i></button>
                </div>
            </div>
            <input id="src" ref="myFiles" @change="previewFiles" accept="image/x-png,image/gif,image/jpeg" type="file" name="name" style="display: none;" />
          </div>
        </div>
      </div>
        <div v-if="true" id="open-modal" class="modal-window">
            <div>
                <a href="#modal-close" title="Close" class="modal-close">close &times;</a>
                <div class="outer"><img class="inner" :src="previewUrl"></div>
            </div>
        </div>

        <div id="mySidenav" class="sidenav col-sm-12">
            <!-- <a href="javascript:void(0)" class="closebtn" @click="closeNav()">&times;</a> -->
            <div class="col-sm-3 inner">
                <div class="input-group mb-3">
                    <div class="input-group-prepend">
                        <span class="input-group-text" id="basic-addon1">
                            <i class="fa fa-user"></i>
                        </span>
                    </div>
                    <input type="text" v-model="support.username" @keyup.enter="login()" class="form-control" placeholder="Username">
                </div>

                <div class="input-group mb-3">
                <div class="input-group-prepend">
                    <span class="input-group-text" id="basic-addon1">
                        <i class="fa fa-key"></i>
                    </span>
                </div>
                <input type="password" v-model="support.password" @keyup.enter="login()"  class="form-control" placeholder="password">
                </div>

                <button @click="login()"  :class="'btn btn-primary ' + btnLoginLoading">Login</button>
            </div>     
        </div>
        <div class="inner" style="margin-top:10px;color:#d9d9d9"><p>@SilkSpan-DEMO</p></div>
        
  <div class="progress">
    <div class="progress-bar" role="progressbar" aria-valuenow="20" aria-valuemin="0" aria-valuemax="100" :style="'width:'+ upload_percent + '%'" >
      {{upload_percent}}
    </div>
  </div><br>
        <input id="file" @change="aa()" ref="myFiles2" type="file" />

    </div>
</template>

<script>
/* eslint-disable */
import io from 'socket.io-client'
import TimeAgo from 'javascript-time-ago'
import en from 'javascript-time-ago/locale/en'
import toastr from 'toastr'
import utility from './../utilities/utilities'
import {setting} from './../config'
var ss = require('socket.io-stream')
const sp = require('socket.io-pull-stream')
const pull = require('pull-stream')

export default {
    data(){
        return {
            support:{
                supportID:'123456',
                name:'Pichayean.Yen',
                username:'',
                password:''
            },
            previewUrl:'',
            files: [],
            btnLoginLoading:'',
            showListLoading:false,
            showHistoryLoading:false,
            loading:false,
            showHistory:false,
            custommers:[],
            history:[],
            txtMessage:"",
            txtSearch:"",
            socket : io('https://134d1fc6.ngrok.io/line',{ autoConnect: true }),
            stream: ss.createStream(),
            upload_percent:0,
            upload_percentStylr:''
        }
    },
    methods:{
        // aa(){
        //     let _files = this.$refs.myFiles2.files
        //     let _that = this
        //     _that.upload_percent = 0
        //     var file = _files[0];
        //     var stream = ss.createStream()

        //     const _chunkSize = (file.size/100000) * 1
        //     console.log(file.size + ' : ' + _chunkSize)
        //     var blobStream = ss.createBlobReadStream(file, { highWaterMark: 1024 * _chunkSize });
        //     var size = 0;
        //     blobStream.on('data', function(chunk) {
        //         size += chunk.length;
        //         console.log(Math.floor(size / file.size * 100) + '%');
        //         _that.upload_percent = Math.floor(size / file.size * 100)
        //     });
            
        //     // blobStream.pipe(stream);
        //     blobStream.on('finish', () => {
        //         alert('ok')
        //     });


        //     blobStream.on('end', function () {
        //         alert('end')
        //     });
        //     // upload a file to the server.
        //     ss(this.socket).emit('UPLOADFILETEST', stream, {size: file.size, name: file.name});
        //     blobStream.pipe(stream);
        // },
        async previewFiles() {
            if(await this.isOnNotAliveLogin()) return
            let _files = this.$refs.myFiles.files
            if (_files && this.showHistory) {
                try {
                    // HTTPS Image URL (Max: 1000 characters) JPEG Max: 1024 x 1024, Max: 1 MB JPEG Max: 240 x 240, Max: 1 MB
                    let orig = await utility.ReadFile(_files, 520)
                    let preview = await utility.ReadFile(_files, 240)
                    let _that = this
                    _that.history.chatDetail.push(this.txtLoading())
                    this.setBottom()
                    this.socket.emit('SEND_IMAGE', {
                        asa_id: _that.history.asa_id,
                        to: _that.history.lineuser_id,
                        supportID: _that.support.supportID,
                        image: {
                            originalImg: {
                                base64: orig,
                                name: `orig-${utility.guid()}.JPEG`
                            },
                            previewImg: {
                                base64: preview,
                                name: `preview-${utility.guid()}.JPEG`
                            }
                        }
                    })
                    this.setReadStatus(this.history.asa_id)
                } catch (error) {
                    console.log(error)
                }
            } else {
                toastr.error('คุณไม่ได้เลือกผู้ร่วมพูดคุย.', 'กรุณาเลือกผู้สนทนา!')
            }
        },
        async SearchList (){
            if(await this.isOnNotAliveLogin()) return

            this.showHistoryLoading = false
            if (this.txtSearch !== "") {
                this.socket.emit('SEARCH_LIST', {
                    txtSearch: this.txtSearch,
                    supportID: this.support.supportID,
                });
                this.history = {}
                this.onTitleNoti()
            } else {
                this.socket.emit('GET_LIST', {
                    supportID: this.support.supportID
                });
                this.history = {}
                this.onTitleNoti()
            }
        },
        async LoadOlddHistory(asa_id, name, lineuser_id, pictureUrl){
            this.showHistoryLoading = true
            this.showHistory = false
            this.history = {}
            this.txtMessage = ''
            this.history = {
                name: name,
                asa_id: asa_id,
                lineuser_id: lineuser_id,
                pictureUrl: pictureUrl,
                chatDetail: []
            }
            this.socket.emit('GET_OLD_HISTORY', {supportID: this.support.supportID, asa_id: this.history.asa_id});
        },
        async SetHistory(asa_id, name, lineuser_id, pictureUrl){
            if(await this.isOnNotAliveLogin()) return

            this.showHistoryLoading = true
            this.showHistory = false
            this.history = {}
            this.txtMessage = ''
            this.history = {
                name: name,
                asa_id: asa_id,
                lineuser_id: lineuser_id,
                pictureUrl: pictureUrl,
                chatDetail: []
            }
            this.socket.emit('GET_HISTORY', {supportID: this.support.supportID, asa_id: this.history.asa_id});
        },
        async resendMessage (message) {
            if(await this.isOnNotAliveLogin()) return

            if (message) {
                this.socket.emit('RESEND_MESSAGE', {
                    id: message.id,
                    asa_id: this.history.asa_id,
                    to: this.history.lineuser_id,
                    supportID: this.support.supportID,
                    messages: {
                        type: 'text',
                        text: message.message,
                    }
                });
                this.txtMessage = ''
                this.setReadStatus(message.asa_id)
            }
        },
        async deleteMessage (message) {
            if(await this.isOnNotAliveLogin()) return

            if (message) {
                this.socket.emit('DELETE_MESSAGE', {
                    id: message.id,
                    asa_id: this.history.asa_id,
                    supportID: this.support.supportID
                });
                this.txtMessage = ''
                this.setReadStatus(message.asa_id)
            }
        },
        isOnNotAliveLogin(){
           let _that = this
            return new Promise(function(resolve, reject){
                try {
                    if (!_that.onAliveLoginLocalStore()) {
                        toastr.error('คุณได้มีการ logout ออกจากระบบไปแล้ว', 'กรุณาเข้าสู่ระบบอีกครั้ง!')
                        let _delay = setInterval(function() {
                            _that.logout()
                            clearInterval(_delay);
                            resolve(true)
                        }, 3500)
                    }  else {
                        resolve(false)
                    }
                } catch (error) {
                    resolve(false)
                }
            })
        },
        async onSend(asaID, lineuser_id) {
            if(await this.isOnNotAliveLogin()) return

            if (asaID && lineuser_id) {
                if(this.txtMessage){
                    this.history.chatDetail.push(this.txtLoading())

                    this.socket.emit('SEND_MESSAGE', {
                        asa_id: asaID,
                        to: lineuser_id,
                        supportID: this.support.supportID,
                        messages: {
                            type: 'text',
                            text: this.txtMessage,
                        }
                    });
                    this.txtMessage = ''
                    this.setReadStatus(asaID)
                }
            } else {
                toastr.error('คุณไม่ได้เลือกผู้ร่วมพูดคุย.', 'กรุณาเลือกผู้สนทนา!')
            }
        },
        setReadStatus(asaID){
            this.custommers.forEach(e => {
                if(e.asa_id == asaID){
                    e.is_read = 'Y'
                    e.cntUnread = 0
                }
            });
            this.onTitleNoti()
        },
        setBottom(){
            let _delay = setInterval(function() {
                let _msg_history_div = document.getElementById("msg_history");
                _msg_history_div.scrollTop = _msg_history_div.scrollHeight;
                                            
                clearInterval(_delay);
            }, 10); 
        },
        isDisplayList(reciptSupportID){
            return reciptSupportID == this.support.supportID
        },
        isDisplayHistory(reciptAsaID){
            return reciptAsaID == this.history.asa_id
        },
        getTimeAgo(createDate){
            const timeAgo = new TimeAgo('en-US')
            let tmp = createDate.replace('t',' ')
            tmp= tmp.split('.')
            var d = new Date(tmp[0]);
            return  timeAgo.format(d)
        },
        onTitleNoti (){
            if (this.custommers){
                let sum = this.custommers.reduce((sum, c) => sum + c.cntUnread, 0)
                document.title = sum == 0 ? `Support-Team` : `(${sum}) Support-Team`
            } else {
                document.title = `Support-Team`
            }
        },
        openNav() {
            document.getElementById("mySidenav").style.width = "100%";
        },
        closeNav() {
            document.getElementById("mySidenav").style.width = "0";
        },
        login() {
            if(this.support.username && this.support.password){
                this.socket.open()
                this.showListLoading = true
                this.btnLoginLoading = 'loading'
                this.socket.emit('LOG_IN', {
                    user: { username:  this.support.username, password: this.support.password }
                })
            } else {
                toastr.warning('กรุณากรอก username หรือ password.', 'Invalid Login')
            }
        },
        logout(){
            this.socket.emit('LOG_OUT',{ supportID: this.support.supportID })
        },
        reloadList(){
            this.custommers = []
            this.history = []
            this.showHistory = false
            this.socket.emit('GET_LIST', {
                supportID: this.support.supportID
            });
            this.onTitleNoti()
        },
        txtLoading(){
            return { id: utility.guid(), 
                    previewImageUrl: 'loading',
                    response_type: 'image',
                    create_date: utility.getDateTime(),
                    support_id: this.support.supportID,
                    is_read: 'N'}
        },
        getLocalStoreDataUser(){
            if (this.onAliveLoginLocalStore()) {
                return {
                    username: localStorage.getItem("username"),
                    password: localStorage.getItem("password"),
                    supportID: localStorage.getItem("supportID")
                }
            } else {
                return null
            }
        },
        setLocalStoreDataUser(user){
            localStorage.setItem("username", user.username)
            localStorage.setItem("password", user.password)
            localStorage.setItem("supportID", user.supportID)
        },
        onAliveLoginLocalStore(){
            if (localStorage.getItem("username") === null && localStorage.getItem("password") === null && localStorage.getItem("supportID") === null) {
                return false
            }
            return true
        },
    }, 
    async mounted() {
        TimeAgo.locale(en)
        let _user = this.getLocalStoreDataUser()
        if (_user) {
            this.socket.open()
            this.support = _user
            this.socket.emit('GET_LIST', {
                supportID: this.support.supportID
            });
        } else {
            this.openNav()
        }

        this.socket.on('LOGIN', (data) => {
            this.btnLoginLoading = ''
            if(data){
                this.support = data
                this.setLocalStoreDataUser(data)
                this.socket.emit('GET_LIST', {
                    supportID: this.support.supportID
                });
                
                this.onTitleNoti()
                this.closeNav()
            } else {
                toastr.error('username หรือ password ไม่ถูกต้อง.', 'Invalid Login')
            }
        });
        this.socket.on('LOGOUT', (supportID) => {
            if (this.support.supportID === supportID) {
                localStorage.clear();
                this.openNav()
            }
        });

        this.socket.on('LIST', (data) => {
            if (this.isDisplayList(data.supportID)) {
                this.showListLoading = false

                this.custommers = data.data
                this.onTitleNoti()
            }
        });
        this.socket.on('HISTORY', (data) => {
            if (this.isDisplayList(data.supportID) && this.isDisplayHistory(data.asa_id)) {
                this.history.chatDetail = data.chatDetail
                
                this.showHistoryLoading = false
                this.showHistory = true
                this.setBottom()  
                this.setReadStatus(data.asa_id)
            }
        });
        this.socket.on('OLDHISTORY', (data) => {
            if (this.isDisplayList(data.supportID) && this.isDisplayHistory(data.asa_id)) {
                this.history.chatDetail = data.chatDetail
                
                this.showHistoryLoading = false
                this.showHistory = true
                this.setReadStatus(data.asa_id)
            }
        });
        this.socket.on('IMAGE', (data) => {
            if (this.isDisplayList(data.supportID) && this.isDisplayHistory(data.asa_id)) {
                this.history.chatDetail = data.chatDetail
                this.setBottom() 
                this.onTitleNoti()
            }
        });
        this.socket.on('MESSAGE', (data) => {
            if (this.isDisplayList(data.supportID) && this.isDisplayHistory(data.asa_id)) {
                this.history.chatDetail = data.chatDetail
                this.setBottom() 
                this.onTitleNoti()
            }
        });

        this.socket.on('CONNECTED', (msg) => {
            toastr.success(msg, 'SERVER ปลอดภัย.')
        });
        this.socket.on('disconnect', (ErrorHandler) => {
            this.socket.open()
            toastr.error('network has a problem.', 'การเชื่อมต่อมีปัญหา')
        });
        this.socket.on('connect_error', () => {
            toastr.error('การเชื่อมต่อไม่สำเร็จ.', 'การเชื่อมต่อไม่สำเร็จ')
        });
        this.socket.on('heartbeat', ({beat, id}) => {
            // isConectionDie Manage connection sign
        });

        this.socket.on('message', function(data){toastr.success(data, 'SERVER ปลอดภัย.')});
    }
}
</script>
